body {
  background-color: #eee9e9;
}
#navigation-bar {
  height: 70px;
  background-color: rgb(0, 0, 0, 0.85);
}
#navigation-bar li {
  list-style: none;
  display: inline;
}
#navigation-bar ul li a {
  float: right;
  display: block;
  color: aliceblue;
  background-color: rgb(236, 232, 247, 0.1);
  font-family: "Merriweather Sans";
  padding: 10px;
  margin: 15px 70px 0px 30px;
  border-radius: 10px;
}
#navigation-bar ul li a:hover,
.active {
  background-color: rgba(255, 255, 255);
  color: black;
}


.dropdown {
  float: right;
  display: block;
  color: rgb(0, 0, 0);
  background-color: rgb(236, 232, 247, 0.1);
  font-family: "Merriweather Sans";
  padding: 0px;
  margin: 15px 70px 0px 30px;
  border-radius: 10px;
  text-decoration: none;
}

.dropdown .dropbtn {
  font-size: 16px;  
  border: none;
  outline: none;
  color: rgb(247, 242, 242);
  padding: 10px;
  background-color: inherit;
  font-family: inherit;
  margin: 0px;
  border-radius: 10px;
  text-decoration: none;
}



.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f8f8f8;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a, p {
  float: none;
  color: rgb(235, 6, 6);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {
  background-color: rgb(10, 10, 10);
  color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

i.fa-bars {
