}
@-webkit-keyframes slide {
  0% {
    left: 0;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -150%;
  }
}
@keyframes slide {
  0% {
    left: 0;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -150%;
  }
}
