@import url("https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Merriweather+Sans:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap");


* {
    padding: 0;
    margin: 0;
    text-decoration: none;
  }

body{
    background-color: #e0ebeb;
    font-family : Verdana,Tahoma, sans-serif;
}

#navigation-bar {
    height: 70px;
    background-color:#001a66;
  }
  #navigation-bar li {
    list-style: none;
    display: inline;
   
  }
  #navigation-bar ul li a {
    float: right;
    display: block;
    color:aliceblue;
    background-color: rgb(236, 232, 247, 0.1);
    font-family: "Merriweather Sans";
    font-weight: bold;
    padding: 10px;
    margin: 15px 70px 0px 30px;
    border-radius: 10px;
  }
  #navigation-bar ul li a:hover,
  .active {
    background-color:rgba(255, 255, 255);
    color:black;
  }
  
  i.fa-bars {
    color: aliceblue;
    float: left;
    font-size: 40px;
    margin: 15px 10px 10px 20px;
  }

.box{
    width: 400px;
    height: auto;
    background-color:rgba(0, 0, 0, 0.7);
    padding: 20px;
    margin: 5% auto 60px;
    text-align: center;
    position: relative;
    box-sizing: border-box;
    flex-direction: column;
}

.box img{
	width:100px;
    margin-top:-50px ;
}

.header {
	width: 300px;
    height: 50px;
    text-transform: uppercase;
	display: inline-flex;
    padding-top: 20px;
    padding-bottom: 0;
	justify-content: space-between;
}

.header a {
    font-size: 20px;
    text-decoration: none;
    color: #ffffff;
	display: inline-flex;
    padding-left: 25px;
    padding-right: 25px;
	justify-content: center;
    cursor:pointer;
}

.header .work {
    color: #9999ff;
	font-weight: bold;
	position: relative;
}

.header .work:after {
	position: absolute;
    border: none;
	content: "";
}

#errorMsg{
    color:red;
    text-align: center;
    font-size: 12px;
    padding-bottom: 20px;

}

.content{
    display: inline-flex;
    overflow: hidden;
}

form {
    position: relative;
    display: inline-flex;
    width: 100%;
    height: 100%;
	flex-shrink: 0;
	flex-direction: column;
	transition: right 0.5s;
}

.login a{
    text-decoration: none;
    color: #ffffff;
    font-size: 15px;
    text-align: center;
    cursor: pointer;
}

.extend form {
	right: 100%;
}

input[type=text], input[type=password], input[type=email]{
	display:block;
    position: relative;
	border: 4px solid #ffffff;
	padding: 12px;
    margin-bottom: 15px;
    width: 100%;
 	box-sizing: border-box;
    font-size: 17px;
    outline: none;
}

input[type=text]:hover,  input[type=password]:hover, input[type=email]:hover{
    border: 4px solid #9999ff;

}

input[type=submit]{
    display:block;
    position: relative;
    border: 3px solid #9999ff;
    padding: 12px;
    margin-bottom: 20px;
    width: 100%;
    border-radius: 25px;
    background-color:#002699 ;
    text-align:center;
    font-size: 20px;
    color: #ffffff;
	cursor:pointer;
    outline: none;
}
	
input[type=submit]:hover{
	 background-color:#ffffff;
     color: #000000;
     font-weight: bold;
}
input[type=checkbox]{
    background-color:#9999ff;
}
#check{
    margin-top: 10px;
    text-align: left;
    color: #ffffff;
    font-size: 15px;
}


  table,
  tr,
  td {
    width: 40%;
    margin: 40px;
    color:#fff;
    font-family: "Merriweather Sans";
    font-size: 15px;
  }
  
  .icons {
    float: right;
  }
  
  .icons img {
    border-radius: 20px;
    margin-left: 20px;
  }
 